/* Header */
@import url('https://fonts.googleapis.com/css2?family=Libre+Caslon+Text&display=swap');
.header{
    font-family: 'Libre Caslon Text', serif;
    margin-top: 15px;
    margin-left: 100px;
    margin-right: 100px;
    background-image: url('../../images/FONDO.png');
    color: #18643B;
    border-radius: .5em;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    border: 10px solid #18643B;
    box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
    & h1 {
      margin-left: 15px;
      margin-right: 15px;
      font-size: 25px;
      text-transform: uppercase;
    }
    & img{
      margin-top: -10px;
      margin-bottom: 5px;
      width: 340px;
    }
}
